import React, {Component} from 'react';
import Info from './subcomponentes/InfoEmpresa.js'
import Cursos from './subcomponentes/Cursos.js'
import Hero from './subcomponentes/HeroEmpresa.js'
import Institucion from './subcomponentes/institucion.js'
import Video from './subcomponentes/Video.js'
import 'bootstrap/dist/css/bootstrap.min.css';

class empresa extends Component{
  render () {
    return (
     <div className="empresa">
       <Hero className="w-100"/> 
       <Info className="w-100" />
       <Video className="w-100" />
       <Cursos className="w-100" />
       <Institucion className="w-100" />
     </div>
    );
  }
}

export default empresa;
