import React, {Component} from 'react';
import { Typography } from 'antd';
import {Container, Row, Col} from 'react-bootstrap'
import 'bootstrap/dist/css/bootstrap.min.css';
import 'antd/dist/antd.css';
import './../../css/Font.css';
import info1 from './../../img/brecha-empresa.jpg'
import info2 from './../../img/dudas.jpg'
const { Title, Paragraph, Text } = Typography;

class Info extends Component {
  render () {
    return (
      <Container className=" bg-white" fluid>
      <div className="mx-auto">
        <Row className="">
          <Col>
              <Title level={2}>
                <div className="Text-h2 text-info text-center p-5">
                  ¿Es el senior un valor agregado a la empresa?
                </div>
              </Title>
          </Col>
        </Row>
        <Row className=" shadow-lg bg-white p-3 mb-3 rounded" lg={2}>
          <Col xs sm md lg={5} xl={5}>
            <Typography>
              <Title level={3}>
                <div className="Text-h3 text-secondary text-left mb-5">
                  El senior y la brecha digital
                </div>
              </Title>

              <Paragraph>
                <Text>
                  <div className="Text-body text-black-50 text-justify">
                    Muchas veces el obstáculo entre el aporte ficticio y real es la BARRERA DIGITAL.
                    Es por esto que buscamos AYUDAR A TU EMPRESA, a obtener un equipo más
                    completo que logre adaptarse a las necesidades actuales. 
                  </div>
                </Text>
              </Paragraph>
            </Typography>
          </Col>
          <Col xs sm md lg={3} xl={3}>
                <div>
                  <img
                    alt="capacitacion"
                    src={info1}
                    width="400"
                    height="280"
                    className="d-inline-block align-top"
                  />
                </div>
          </Col>
        </Row>
        <Row className="shadow-sm bg-white p-5"  lg={2}>
          <Col xs sm md lg={6} xl={6}>
                <div>
                  <img
                    alt="capacitacion"
                    src={info2}
                    width="400"
                    height="225"
                    className="d-inline-block align-top"
                  />
                </div>
          </Col>
          <Col xs sm md lg={5} xl={5}>
            <Typography>
              <Title level={3}>
                <div className="Text-h3 text-secondary text-right mb-5">
                  PLataformas digitales, un océano de incertidumbre
                </div>
              </Title>
              <Text>
                <div className="Text-body text-black-50 text-justify">
                  Aunque nosotros tambien seamos una plataforma digital somos
                  conscientes que muchas personas pero sobre todo SENIORS pueden
                  perderse entre tanto contenido, por eso contamos de equipo
                  capacitado para hacer un ambiente amigable a los seniors.
                </div>
              </Text>
            </Typography>
          </Col>
        </Row>
      </div>
      </Container>
    );
  }
}

export default Info;
