import React, {Component} from 'react';
import { Typography } from 'antd';
import { Card, Col, Row } from 'antd';
import { Container } from 'react-bootstrap'
import 'antd/dist/antd.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import '../../css/Exp.css'
import './../../css/Font.css';
import img1 from './../../img/basico.jpg';
import img2 from './../../img/intermedio.jpg';
import img3 from './../../img/avanzado.jpg';
import { Button } from 'antd';
const { Title, Text } = Typography;

class ExpZenior extends Component {
  render () {
    return (
      <div id="Exp" className="site-card-wrapper bg-light mt-5 mb-s">
        <Container className="padding-0" fluid>
            <Title level={2}>
              <div className="Text-h3 text-info text-center">
                PAQUETES ZENIOR
              </div>
            </Title>
          <Row justify="center" gutter={14}> 
            <Col className="m-3  text-center" md={1} lg={6}>
              <Card
                className="bg-dark card-itemE p-3"
                title="Básico"
                bordered={false}
                cover={
                  <img
                    style={{height:200}}
                    alt="example"
                    src={img1}
                  />
                }
              >
              <Button type="primary">COMPRAR PAQUETE</Button>
              </Card>
            </Col>
            <Col className="m-3  text-center" md={1} lg={6}>
              <Card
                className="bg-dark card-itemE p-3"
                title="Intermedio"
                cover={
                  <img
                    style={{height:200}}
                    alt="example"
                    src={img2}
                  />
                }
              >
              <Button type="primary">COMPRAR PAQUETE</Button>
              </Card>
            </Col>
            <Col className="m-3  text-center" md={1} lg={6}>
              <Card
                className="bg-dark card-itemE p-3"
                title="Avanzado"
                cover={
                  <img
                    style={{height:200}}
                    alt="example"
                    src={img3}
                  />
                }
              >
              <Button type="primary">COMPRAR PAQUETE</Button>
              </Card>
            </Col>
          </Row>
        </Container>
      </div>
    );
  }
}

export default ExpZenior;
