import React, {Component} from 'react';
import {Container, Row, Col} from 'react-bootstrap'
import { Button, Typography } from 'antd';
import 'bootstrap/dist/css/bootstrap.min.css';
import 'antd/dist/antd.css';
const { Title, Paragraph, Text } = Typography;
class Institucion extends Component{
  render () {
    return (
     <Container className="Instituciones p-5 bg-info" fluid>
        <div className="h-100 w-100">
          <Row className="mx-auto w-100 text-center" lg={1}>
           <Col xs sm md lg={12} xl={12}>
              <Title level={3}>
                <div className="text-white">
                  ¡Ayúdanos a <span className="text-dark">DIGITALIZAR</span> seniors, si eres una institución puedes contactarnos
                  para brindar apoyo a estas personas! 
                </div>
              </Title>
           </Col>
           <Col  className="mx-auto" xs sm md lg={12} xl={12}>
           	  <button type="button" className="btn btn-outline-light" > CONTÁCTANOS </button>
           </Col>
          </Row>

        </div>
      </Container>
    );
  }
}

export default Institucion;
